@import url(https://fonts.googleapis.com/css2?family=Quantico:wght@400;700&display=swap);
body{scroll-behavior:smooth;font-size:16px;margin:0;font-family:-apple-system, BlinkMacSystemFont, "Quantico", "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;background-color:#000;height:100vh;color:#fff;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body *{box-sizing:border-box;margin:0;padding:0}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}a{color:#fff}a:hover{color:#fff;text-decoration:none;transition:all 0.5s ease;color:rgba(255,255,255,0.685)}ul{list-style:none;padding:0;margin:0;display:flex}

.navbar__menu{font-size:14px;display:flex;height:100%;width:35%;justify-content:space-between}.navbar__menu .menu__item{text-transform:uppercase;height:100%;display:flex;align-items:center;padding:0 20px;transition:all 0.3s ease;cursor:pointer}.navbar__menu .menu__item:hover{background-color:#1f1f1f}.navbar__menu .menu__item:hover a{color:#fff}.navbar__menu .menu__item a{color:#969696;transition:all 0.3s ease}.navbar__menu .not-empty{position:relative}.navbar__menu .not-empty::before{content:"";width:7px;height:7px;border-radius:50%;background-color:#fff;position:absolute;bottom:40px;right:10px}

.header__navbar{background-color:#0f0f0f;height:80px;padding:0}.header__navbar .container{height:100%}.header__navbar .navbar__logo{font-size:36px;text-transform:uppercase}

.basket-list{margin-top:50px}.basket-list .list-group-item{background-color:#0f0f0f}

.basket h1{padding-top:50px;border-bottom:2px solid #fff;padding-bottom:15px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.basket .alert{margin-top:25px}

.goods,.suits,.shoes{padding-top:50px}.goods img,.suits img,.shoes img{width:100%;max-width:100%;background-color:#191b1c}.goods h1,.suits h1,.shoes h1{border-bottom:2px solid #fff;padding-bottom:15px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.goods .jumbotron,.suits .jumbotron,.shoes .jumbotron{background-color:#0f0f0f;margin-top:15px}.goods .jumbotron .card-body,.suits .jumbotron .card-body,.shoes .jumbotron .card-body{background-color:#191b1c}.to-top-btn{position:fixed;height:35px;width:35px;font-size:25px;bottom:50px;right:50px;background-color:#191b1c;padding:25px;border-radius:50%;display:flex;justify-content:center;align-items:center}

.brands{background-color:#0f0f0f;padding:60px 0}.brands__header{text-transform:uppercase;font-size:36px;border-bottom:2px solid #fff;padding-bottom:40px}.brands__list{margin-top:40px;width:100%;display:flex;justify-content:space-between}.brands__list .list__item img{width:250px}.brands .container{display:flex;flex-direction:column;align-items:center}

.content__header{text-transform:uppercase;border-bottom:2px solid #fff;padding-bottom:50px;z-index:2}.content__watches{display:flex;justify-content:space-between;margin-top:50px}.content__watches .watches__item img{background-color:#191b1c;width:250px;max-width:100%}.content__watches .watches__item p{font-size:18px;margin-top:25px}.content .col-6:first-child{padding:93px 80px;display:flex;flex-direction:column;align-items:center}.content .col-6:nth-child(2){background:rgba(0,0,0,0.4) url("https://bit.ly/35B3Mb2") no-repeat top left/cover;background-blend-mode:darken;background-size:100%;display:flex;flex-direction:column;align-items:center;justify-content:flex-end;padding-bottom:100px}.content .col-6:nth-child(2) a{margin-top:30px;text-transform:uppercase;font-size:18px;padding:12px 28px;border:2px solid #fff}.content .col-6:nth-child(2) a:hover{background-color:#fff;color:#000}

.footer{padding:17px 0;color:#d0d0d0}.footer__developer{text-decoration:underline}

.header__hero{height:100%;display:flex;flex-direction:column;justify-content:center;align-items:center;position:relative}.header__hero .overlay{position:absolute;height:100%;width:100%;top:0;left:0;background-color:#000;opacity:0.4}.header__hero .hero__logo{z-index:2;border:4px solid #fff;width:320px;height:320px;display:flex;flex-direction:column;justify-content:center;align-items:center}.header__hero .hero__logo h1{font-size:64px;text-transform:uppercase;border-bottom:2px solid #fff}.header__hero .hero__logo p{font-size:22px;text-transform:uppercase}.header__hero .hero__text{z-index:2;width:50%;text-align:center;margin-top:80px}

.header{height:100vh;background:url(/porten/static/media/header-bg.b97a6489.jpg) no-repeat center;background-size:cover;display:flex;flex-direction:column}

